import axios from "axios";
import {
  CANEAL_TOCKEN_STATUS,
  NETWORK_ERROR_STATUS,
  ACCESS_TOKEN,
  REFRESH_TOKEN,
} from "@/utils/common-def.js";
import { lostorage, getUId } from "@/utils/common-func.js";
import { login } from "@/api/api";

const errorHandle = (status, msg) => {
  console.log("errorHandle", status, msg);
  // 
  switch (status) {
    case 401:
      return msg || "";
    case 403:
      return msg || "";
    case 404:
      return msg || "";
    default:
      return msg || "";
  }
};
let isRefreshing = false; //  token token
let requests = []; // 

const initLogin = async () => {
  let resp;
  try {
    resp = await login();
  } catch (error) {
    initLogin();
    return;
  }

  lostorage.setItem({
    name: ACCESS_TOKEN,
    value: resp.token,
    expires: resp.token_expire_unix * 1000,
  });
  lostorage.setItem({
    name: REFRESH_TOKEN,
    value: resp.token_refresh_unix,
    expires: resp.token_refresh_unix * 1000,
  });
};
// axios
const instance = axios.create({
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 1000 * 300,
  headers: {},
});

// 
instance.interceptors.request.use(
  (config) => {
    const accessToken = lostorage.getItem(ACCESS_TOKEN);
    if (accessToken) {
      config.headers["Authorization"] = accessToken; // 
      config.headers["Nonce-Str"] = getUId(); // 16
    }

    return config;
  },
  (error) => Promise.reject(error)
);

// 
instance.interceptors.response.use(
  // 
  (resp) => {
    if (resp.status !== 200 || !resp.data) {
      return Promise.reject({
        ...resp,
        msg: `status${resp.status} - data${resp.data}`,
      });
    } else {
      const retData = resp.data;

      //  token
      if (
        retData.code === 101 &&
        !resp.config.url.includes("/kfh5/user/token/refresh")
      ) {
        //refreshToken
        if (
          !lostorage.getItem(REFRESH_TOKEN) &&
          lostorage.getItem(ACCESS_TOKEN)
        ) {
          console.log("refreshToken", resp.config.url);
          //
          if (!isRefreshing) {
            isRefreshing = true;
            return axios({
              url: "/auth/refresh-token",
              method: "post",
              headers: {
                Authorization: lostorage.getItem(ACCESS_TOKEN),
                ["Nonce-Str"]: getUId(),
              },
            })
              .then((res) => {
                const resData = res.data;

                if (resData.code == 0) {
                  //token
                  resp.config.headers.Authorization = resData.data.accessToken;

                  lostorage.setItem({
                    name: ACCESS_TOKEN,
                    value: resData.data.token,
                    expires: resData.data.token_expire_unix * 1000,
                  });
                  lostorage.setItem({
                    name: REFRESH_TOKEN,
                    value: resData.data.token_refresh_unix,
                    expires: resData.data.token_refresh_unix * 1000,
                  });

                  //
                  requests.forEach((cb) => cb(resData.data.accessToken));
                  console.log("refresh response=>", requests, resp.config.url);
                  requests = []; // 
                  return instance(resp.config); //
                } else {
                  console.log("code0");
                  initLogin();
                  return Promise.reject({
                    status: CANEAL_TOCKEN_STATUS,
                    msg: "",
                  });
                }
              })
              .catch(() => {
                console.log("");

                initLogin();
                return Promise.reject({
                  status: CANEAL_TOCKEN_STATUS,
                  msg: "",
                });
              })
              .finally(() => {
                isRefreshing = false;
              });
          } else {
            //  resolve  Promise
            return new Promise((resolve) => {
              //  resolve 
              requests.push((accessToken) => {
                resp.config.headers.Authorization = accessToken;
                resolve(instance(resp.config));
              });
            });
          }
        }
      }

      // 
      if (retData.code !== 0) {
        return Promise.reject({
          ...resp,
          code: retData.code,
          msg: retData.msg,
        });
      }

      return Promise.resolve(retData.data);
    }
  },
  (error) => {
    const { response } = error;
    if (response) {
      // 2xx
      const message = errorHandle(response.status, response.data.message);
      console.log("=>", "666");

      return Promise.reject({
        ...response,
        msg: `${response.status}${message}`,
      });
    } else {
      console.log(" ");
      return Promise.reject({
        status: NETWORK_ERROR_STATUS,
        msg: " ",
      });
    }
    // return Promise.reject(error);
  }
);

export default instance;
