/**
 * listobject
 * @param obj 
 * @param lists list
 * @param keyName listkey
 * @returns objects
 */
export const List2Objects = function (lists, keyName) {
  const tmpObjects = {};
  lists.forEach((item) => {
    const keyValue = item[keyName];
    tmpObjects[keyValue] = item;
  });

  return tmpObjects;
};

