} from "@/utils/common-def";

export default {
  name: "HomeChat",
  data() {
    return {
      CHAT_ROLE_TYPE_ENUM,
      RIDE_PAY_TYPE_LIST,
      CHAT_CONTENT_TYPE_ENUM,
      WAIT_STATUS_ENUM,
      timestamp2String,

      pageLoading: true,

      //
      chatInputContent: "",
      chatInputFocus: false,

      // id
