      /**
       * role 
       * contentType 
       * content 
       * sendSuss 
       * sendLoading 
       * showMoreBtn: 
       * moreId: id
       * customId: id
       * selfId: id
       */
      chatList: [],
      channelId: "",
      chatLoading: false,
      chatFinished: false,
      chatRefreshing: false,

      // 
      picVisible: false,
      picSrc: "",

      // 
      leaveMessageVisible: false,
      leaveMessageSuccVisible: false,
      leaveInfo: {
        contact_info: "",
        issue_desc: "",
      },

      // 
      rideRecordVisible: false,
      rideTabList: {
        tabIndex: 0,
        tabList: [
          {
            loading: false,
            finished: false,
            refreshing: false,
            label: RIDE_PAY_TYPE_OBJECT[RIDE_PAY_TYPE_ENUM.zfb].label,
            value: RIDE_PAY_TYPE_ENUM.zfb,
            list: [],
            page: 1,
            size: PAGE_SIZE_DEFAULT,
          },
          {
            loading: false,
            finished: false,
            refreshing: false,
            label: RIDE_PAY_TYPE_OBJECT[RIDE_PAY_TYPE_ENUM.wx].label,
            value: RIDE_PAY_TYPE_ENUM.wx,
            list: [],
            page: 1,
            size: PAGE_SIZE_DEFAULT,
          },
          {
            loading: false,
            finished: false,
            refreshing: false,
            label: RIDE_PAY_TYPE_OBJECT[RIDE_PAY_TYPE_ENUM.union].label,
            value: RIDE_PAY_TYPE_ENUM.union,
            list: [],
            page: 1,
            size: PAGE_SIZE_DEFAULT,
          },
          {
            loading: false,
            finished: false,
            refreshing: false,
            label: RIDE_PAY_TYPE_OBJECT[RIDE_PAY_TYPE_ENUM.digital].label,
            value: RIDE_PAY_TYPE_ENUM.digital,
            list: [],
            page: 1,
            size: PAGE_SIZE_DEFAULT,
          },
          {
            loading: false,
            finished: false,
            refreshing: false,
            label: RIDE_PAY_TYPE_OBJECT[RIDE_PAY_TYPE_ENUM.wxQrocode].label,
            value: RIDE_PAY_TYPE_ENUM.wxQrocode,
            list: [],
            page: 1,
            size: PAGE_SIZE_DEFAULT,
          },
        ],
      },

      // 
      userProfile: {
        user_id: "",
        nickname: "",
        avatar_url: "",
      },

      // 
      chatInfo: {
        session_id: "", // session_id
        chat_window: {
          name: DEFAULT_TITLE,
          notes: "",
          welcome_message: "",
          prompt: DEFAULT_PROMPT,
          robot: {
            nickname: "",
            avatar_url: "",
          },
        },
      },

      // banner
      bannerInfo: {
        image_url: "",
      },

      // 
