      },
      // 
      relateIssueLoading: false,
      // 
      waitInfo: {},
      // 
      waitList: [],
      // 
      kfReply: {
        timer: null,
        time: 180,
      },
      // 
      messageQuitTimer: null,
      messageQuitTime: 0,
      messageQuitTipEnd: false,
    };
  },
  created() {
    // let u = navigator.userAgent;
    // if (u.indexOf("iPhone") > -1) {
    //   inobounce.enable();
    // }
  },
  watch: {},
  computed: {
    disabledLeaveSubmit() {
      if (
        checkPhone(this.leaveInfo.contact_info) &&
        this.leaveInfo.issue_desc
      ) {
        return false;
      }
      return true;
    },
  },
  beforeDestroy() {
    // inobounce.disable();
  },
  async mounted() {
    if (!lostorage.getItem(ACCESS_TOKEN)) {
      // 
      try {
        await this.initLogin();
      } catch (error) {
        this.$router.replace("/error");
        return;
      }
    }

    // 1.
    try {
      await this.init();
    } catch (error) {
      if (error.message == NETWORK_ERROR_STATUS) {
        this.$toast({
          message: " ",
        });
      } else if (error.message != CANEAL_TOCKEN_STATUS) {
        this.$toast({
          message: error.message,
        });
      }

      return;
    }

    // 30s
    this.sessionHeartbeat();
    setInterval(() => {
      this.sessionHeartbeat();
    }, 30 * 1000);

    // 
    window.addEventListener("onbeforeunload", () => {
      this.messageQuit();
    });

    // 2.
    this.goEnd(() => {
      this.pageLoading = false;
    });
    // 3.id
    this.setFirstMsgId();
    // 4.
    this.initMoreBtn();
    // 5.homepadding
    const elementResizeDetectorMaker = require("element-resize-detector");
    const erd = elementResizeDetectorMaker();
    erd.listenTo(
      document.getElementsByClassName("chat-footer"),
      function (element) {
        var height = element.offsetHeight;
        document.getElementsByClassName("home")[0].style["padding-bottom"] =
          height + 0 + "px";
      }
    );
  },
  methods: {
    // 
    async init() {
      const params = getQueryValue(
        "https://www.baidu.com/?uid=admin&rid=1&fid=2&channel="
      );
      if (!params.channel) {
        throw new Error("");
      }

      this.channelId = params.channel;

      try {
        await this.initChatInfo();
        await this.initUserProfile();
        await this.initBanner();
        for (const rideItem of this.rideTabList.tabList) {
          this.initRideList({
            page: rideItem.page,
            size: rideItem.size,
            pay_method: rideItem.value,
          });
        }
        if ([WAIT_STATUS_ENUM.fail].includes(this.chatInfo.state)) {
          return;
        }

        // 
        let messageList = await this.getMessage({
          mode: PULL_MESSAGE_TYPE_ENUM.prev,
          start_message_id: "",
        });
        messageList = this.filterMessage(messageList);
        console.log("=======tmessageList", messageList);
        this.chatList.unshift(...messageList);
        this.addChatTime(this.chatList);
        await this.initHotIssueList();

        this.goEnd();
        // 
        // this.getMessageRegularTime();
      } catch (error) {
        throw new Error(error.message);
      }
    },
    // 
    async initLogin() {
      console.log("initLogin invoke");

      let resp;
      try {
        resp = await login();
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }

      lostorage.setItem({
        name: ACCESS_TOKEN,
        value: resp.token,
        expires: resp.token_expire_unix * 1000,
      });
      lostorage.setItem({
        name: REFRESH_TOKEN,
        value: resp.token_refresh_unix,
        expires: resp.token_refresh_unix * 1000,
      });
    },
