    // 
    async getMessageRegularTime() {
      // 
      clearInterval(this.kfReply.timer);
      this.kfReply.timer = null;
      this.kfReply.time = 180;

      this.kfReply.timer = setInterval(async () => {
        // 
        this.kfReply.time--;
        if (this.kfReply.time <= 0) {
          console.log(
            "======--->"
          );
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content:
              "",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.roundTip,
            customId: getUId(),
            content: "",
          });
          this.goEnd(null, 100);
          clearInterval(this.kfReply.timer);
          this.kfReply.timer = null;
          // 
          this.switchStaff();
          return;
        }
      }, 1000);

      if (this.kfReply.time <= 0) {
        return;
      }
      const startItem = deepClone(this.chatList)
        .reverse()
        .find((i) => i.selfId);
      let messageList = await this.getMessage({
        mode: PULL_MESSAGE_TYPE_ENUM.next,
        start_message_id: startItem ? startItem.message_id : "",
      });
      messageList = this.filterMessage(messageList);

      if (this.kfReply.time <= 0) {
        return;
      }
      console.log("=======getMessageRegularTime", messageList);

      const tempList = [];
      messageList.forEach((j) => {
        // 
        const chatList = this.chatList.filter(
          (i) => !i.selfId && i.role == CHAT_ROLE_TYPE_ENUM.user
        );

        const text = chatList.find(
          (i) =>
            i.contentType == CHAT_CONTENT_TYPE_ENUM.text &&
            j.contentType == CHAT_CONTENT_TYPE_ENUM.text &&
            i.content.text == j.content.text
        );
        const image = chatList.find(
          (i) =>
            i.contentType == CHAT_CONTENT_TYPE_ENUM.image &&
            j.contentType == CHAT_CONTENT_TYPE_ENUM.image &&
            i.content.image == j.content.image
        );
        const ride = chatList.find(
          (i) =>
            i.contentType == CHAT_CONTENT_TYPE_ENUM.ride &&
            j.contentType == CHAT_CONTENT_TYPE_ENUM.ride &&
            i.content.ride_record.id == j.content.ride_record.id
        );
        if (text || image || ride) {
          // return
          const replaceIndex = this.chatList.findIndex(
            (i) => i.customId == (text || image || ride).customId
          );
          if (replaceIndex > -1) {
            this.chatList[replaceIndex] = { ...(text || image || ride), ...j };
          }
        } else {
          // push
          tempList.push(j);
        }
      });

      this.chatList.push(...tempList);
      this.addChatTime(this.chatList);
      this.initMoreBtn();
      this.goEnd();

      this.getMessageRegularTime();
    },
    // 
    async handleReSendMeaasge(item) {
      console.log("=======handleReSendMeaasge", item);

      item.sendLoading = true;

      if (item.contentType == CHAT_CONTENT_TYPE_ENUM.text) {
        const req = {
          message: item.content,
          type: CHAT_CONTENT_TYPE_ENUM.text,
        };
        try {
          await this.createMessage(req);
        } catch (error) {
          item.sendSuss = false;
          return;
        } finally {
          item.sendLoading = false;
        }
        item.sendSuss = true;

        // 
        if (this.relateIssueLoading) {
          return;
        }
        this.relateIssueLoading = true;

        await timeout(1000);
        this.getRelateIssueList(item.content);
      }
      if (item.contentType == CHAT_CONTENT_TYPE_ENUM.image) {
        let uploadResp;
        try {
          uploadResp = await uploadFile({
            session_id: this.chatInfo.session_id,
            fileData: item.file,
          });
        } catch (error) {
          item.sendSuss = false;
          item.sendLoading = false;

          return;
        }

        const req = {
          message: uploadResp.download_url,
          type: CHAT_CONTENT_TYPE_ENUM.image,
        };
        try {
          await this.createMessage(req);
        } catch (error) {
          item.sendSuss = false;
          return;
        } finally {
          item.sendLoading = false;
        }
        item.sendSuss = true;
      }
      if (item.contentType == CHAT_CONTENT_TYPE_ENUM.ride) {
        const req = {
          message: item.selfId,
          type: CHAT_CONTENT_TYPE_ENUM.ride,
        };

        try {
          await this.createMessage(req);
        } catch (error) {
          item.sendSuss = false;
          return;
        } finally {
          item.sendLoading = false;
        }

        item.sendSuss = true;
      }
      // 1find2push
      const deleteIndex = this.chatList.findIndex(
        (i) => i.customId == item.customId
      );
      if (deleteIndex > -1) {
        this.chatList.splice(deleteIndex, 1);
      }
    },
    // 
    async switchStaff() {
      let switchSuss = false;
      try {
        const resp = await this.msqueueJoin();
        console.log("==========msqueueJoin", resp);

        if (resp.state == WAIT_STATUS_ENUM.success) {
          // 
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "- -",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.staff,
            contentType: CHAT_CONTENT_TYPE_ENUM.text,
            content: {
              text: `${resp.staff_info.nickname}`,
            },
            customId: getUId(),
            sendSuss: true,
            sendLoading: false,
            showMoreBtn: false,
            moreId: getUId(),
            kfId: resp.staff_info.user_id,
            kfname: resp.staff_info.nickname,
            kfAvatar: resp.staff_info.avatar_url,
          });
          switchSuss = true;
          this.chatInfo.session_id = resp.session_id;
          this.watchChatActive();
        } else if (resp.state == WAIT_STATUS_ENUM.inqueue) {
          // 
          if (this.waitInfo.in_queue_count === resp.in_queue_count) {
            //
            return;
          }
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.roundTip,
            customId: getUId(),
            content: `${resp.in_queue_count}`,
          });
        } else if (resp.state == WAIT_STATUS_ENUM.fail) {
          // 
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.leaveMsgBtn,
            customId: getUId(),
            content:
              "",
          });
          switchSuss = true;
        }
        this.goEnd(null, 100);
        this.waitInfo = resp;
        // /
        if (switchSuss) {
          return;
        }
        // 
        this.waitSwtichStaff(true);
      } catch (error) {
        if (error.message == NETWORK_ERROR_STATUS) {
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "-   -",
          });
          this.goEnd(null, 100);
        } else if (error.message != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.message,
          });
        }
        return;
      }
    },
