    async handleChangeFile(e) {
      console.log("=>", e, e.target.files);

      const files = e.target.files;
      if (!files.length) {
        return;
      }
      if (files.length > 9) {
        this.$toast("9");
        return;
      }
      let verifiTypePass = true;
      for (let index = 0; index < files.length; index++) {
        const file = files[index];
        if (!/\/(jpg|jpeg|png|gif)$/.test(file.type)) {
          this.$toast("");
          verifiTypePass = false;
          break;
        }
      }
      if (!verifiTypePass) {
        return;
      }

      for (let index = 0; index < files.length; index++) {
        const file = files[index];

        const reader = new FileReader();
        reader.onload = async (e) => {
          const meassageItem = {
            role: CHAT_ROLE_TYPE_ENUM.user,
            contentType: CHAT_CONTENT_TYPE_ENUM.image,
            content: {
              image: e.target.result,
            },
            sendLoading: true,
            sendSuss: true,
            customId: getUId(),
            file: file,
          };
          this.chatList.push(meassageItem);
          this.goEnd();

          let uploadResp;
          try {
            uploadResp = await uploadFile({
              session_id: this.chatInfo.session_id,
              fileData: file,
            });
          } catch (error) {
            meassageItem.sendSuss = false;
            meassageItem.sendLoading = false;
            return;
          }
          const req = {
            message: uploadResp.download_url,
            type: CHAT_CONTENT_TYPE_ENUM.image,
          };
          try {
            await this.createMessage(req);
          } catch (error) {
            meassageItem.sendSuss = false;
            return;
          } finally {
            meassageItem.sendLoading = false;
          }
          meassageItem.sendSuss = true;
        };
        reader.readAsDataURL(file);
      }
    },
    // 
    async handleSendRide(item) {
      console.log("=>", item);

      const meassageItem = {
        role: CHAT_ROLE_TYPE_ENUM.user,
        customId: getUId(),
        id: getUId(),
        contentType: CHAT_CONTENT_TYPE_ENUM.ride,
        content: {
          ride_record: item,
        },
        sendLoading: true,
        sendSuss: true,
      };

      this.chatList.push(meassageItem);
      this.goEnd();
      this.handleRideRecordHide();

      const req = {
        message: item.selfId,
        type: CHAT_CONTENT_TYPE_ENUM.ride,
      };

      try {
        await this.createMessage(req);
      } catch (error) {
        meassageItem.sendSuss = false;
        return;
      } finally {
        meassageItem.sendLoading = false;
      }

      meassageItem.sendSuss = true;
    },
    // 
    async handleChatClose() {
      // 
      if (!this.waitInfo.session_id) {
        return;
      }

      const req = {
        session_id: this.waitInfo.session_id,
      };

      try {
        await msqueueClose(req);
      } catch (error) {
        if (error.status == NETWORK_ERROR_STATUS) {
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "-   -",
          });
          this.goEnd(null, 100);
        } else if (error.status != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.msg,
          });
        }

        return;
      }

      this.chatList.push({
        role: CHAT_ROLE_TYPE_ENUM.defaultTip,
        customId: getUId(),
        content: "- ! -",
      });
      this.chatList.push({
        role: CHAT_ROLE_TYPE_ENUM.evaluate,
        customId: getUId(),
        content: 0,
        session_id: this.waitInfo.session_id,
      });
      this.initMessageInfo();
      this.waitList.push(this.waitInfo);
      this.waitInfo = {};
      this.goEnd(null, 100);
    },
    // 
    async handleChangeRate(e, item) {
      window.close();
      return;
      console.log("=>", e, item);
      const req = {
        session_id: item.session_id, // idid
        star_count: e,
      };

      try {
        await msqueueEvaluate(req);
      } catch (error) {
        if (error.status == NETWORK_ERROR_STATUS) {
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "-   -",
          });
          this.goEnd(null, 100);
        } else if (error.status != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.msg,
          });
        }
        item.content = 0;
        return;
      }

      this.chatList.push({
        role: CHAT_ROLE_TYPE_ENUM.defaultTip,
        customId: getUId(),
        content: "-  -",
      });
      this.goEnd(null, 100);
    },
    // 
    async handldeSubmitLeave() {
      if (!this.leaveInfo.contact_info) {
        this.$toast({
          message: "",
        });
        return;
      }
      if (!checkPhone(this.leaveInfo.contact_info)) {
        this.$toast({
          message: "",
        });
        return;
      }
      if (!this.leaveInfo.issue_desc) {
        this.$toast({
          message: "",
        });

        return;
      }

      // if (this.disabledLeaveSubmit) {
      //   return;
      // }

      const req = {
        session_id: this.chatInfo.session_id,
        contact_info: this.leaveInfo.contact_info,
        issue_desc: this.leaveInfo.issue_desc,
      };

      try {
        await messageLeave(req);
      } catch (error) {
        if (error.status != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.msg,
          });
        }
        return;
      }

      this.leaveMessageVisible = false;
      this.leaveMessageSuccVisible = true;
      await timeout(300);
      this.leaveInfo.contact_info = "";
      this.leaveInfo.issue_desc = "";
    },
    async onLoad() {},
    // 
    async handleLoadHistoryList() {
      const startItem = deepClone(this.chatList).find((i) => i.selfId);
      let messageList = await this.getMessage({
        mode: PULL_MESSAGE_TYPE_ENUM.prev,
        start_message_id: startItem.message_id,
      });

      messageList = this.filterMessage(messageList);
      if (!messageList.length) {
        this.chatFinished = true;
        this.chatRefreshing = false;
        this.chatList.unshift({
          role: CHAT_ROLE_TYPE_ENUM.marginTip,
          customId: getUId(),
          content: "~",
        });
        return;
      }
      this.chatList.unshift(...messageList);
      this.addChatTime(this.chatList, true);

      const loadingHeight = document.getElementsByClassName(
        "van-loading van-loading--circular"
      )[0].offsetHeight;
      this.chatRefreshing = false;

      const dom = document.querySelector(".chat-container");
      this.initMoreBtn();
      this.$nextTick(() => {
        const firstMessage = document.getElementById(this.firstMsgId);

        firstMessage.setAttribute("tabindex", "1");
        firstMessage.style.outline = "none";
        dom.scrollTop =
          document.getElementById(`${this.firstMsgId}`).offsetTop -
          loadingHeight;

        this.setFirstMsgId();
      });
    },
    // 
    addChatTime(arr, loadHistory) {
      let createUnix;

      if (!arr.length) {
        return;
      }
      if (loadHistory) {
        let startIndex = JSON.parse(JSON.stringify(arr)).findIndex(
          (i) => i.tips
        );

        for (let index = startIndex; index >= 0; index--) {
          const i = arr[index];

          if (index === startIndex) {
            createUnix = i.createUnix;
          } else {
            if (i.createUnix && i.createUnix + 5 * 60 <= createUnix) {
              console.log("5=>", timestamp2String(i.createUnix), arr);

              arr.splice(index, 0, {
                createUnix: i.createUnix,
                tips: timestamp2String(i.createUnix),
                role: CHAT_ROLE_TYPE_ENUM.defaultTip,
                content: timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm"),
              });
              // 
              createUnix = i.createUnix;
            }
          }
        }

        return;
      } else {
        let startIndex = JSON.parse(JSON.stringify(arr))
          .reverse()
          .findIndex((i) => i.tips);
        if (startIndex < 0) {
          startIndex = 0;
        } else {
          startIndex = arr.length - 1 - startIndex;
        }

        for (let index = startIndex; index < arr.length; index++) {
          const i = arr[index];

          if (index === startIndex) {
            arr.splice(index, 0, {
              createUnix: i.createUnix,
              tips: timestamp2String(i.createUnix),
              role: CHAT_ROLE_TYPE_ENUM.defaultTip,
              customId: getUId(),
              content: timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm"),
            });
            createUnix = i.createUnix;
          } else {
            if (i.createUnix >= createUnix + 5 * 60) {
              console.log(
                "5=>",
                i.createUnix,
                createUnix + 5 * 60,
                timestamp2String(i.createUnix),
                timestamp2String(createUnix + 5 * 60)
              );

              arr.splice(index, 0, {
                createUnix: i.createUnix,
                tips: timestamp2String(i.createUnix),
                role: CHAT_ROLE_TYPE_ENUM.defaultTip,
                customId: getUId(),
                content: timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm"),
              });
              // 
              createUnix = i.createUnix;
            }
          }
        }
      }
    },
    // tab
    async handleRideSlideChange() {
      const tabItem = this.rideTabList.tabList[this.rideTabList.tabIndex];
      console.log("=>", tabItem);
    },
    // 
    async rideOnRefresh(tabItem) {
      console.log("rideOnRefresh=>", tabItem);

      const rideItem = this.rideTabList.tabList.find((i) => {
        return i.value == tabItem.value;
      });
      rideItem.page = 1;

      try {
        await this.initRideList({
          page: rideItem.page,
          size: rideItem.size,
          pay_method: rideItem.value,
          refresh: true,
        });
      } catch (error) {
        if (error.status != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.msg,
          });
        }
        rideItem.refreshing = false;
        rideItem.finished = false;
        return;
      }
      rideItem.refreshing = false;
      rideItem.finished = false;
    },
    async handleMessageScorll() {
      return;
      const dom = document.querySelector(".chat-container");

      const scrollTop = dom.scrollTop;
      if (scrollTop === 0) {
        console.log("=>");

        let messageList = await this.getMessage({
          mode: PULL_MESSAGE_TYPE_ENUM.prev,
        });
        messageList = this.filterMessage(messageList);
        if (!messageList.length) {
          return;
        }
        this.chatList.unshift(...messageList);
        this.initMoreBtn();
        this.$nextTick(() => {
          console.log(
            "=>",
            document.getElementById(`${this.firstMsgId}`).offsetTop
          );
          const firstMessage = document.getElementById(this.firstMsgId);
          firstMessage.setAttribute("tabindex", "1");
          firstMessage.style.outline = "none";
          dom.scrollTop = document.getElementById(
            `${this.firstMsgId}`
          ).offsetTop;

          this.setFirstMsgId();
        });
      }
    },
    // 
    async handleSwitchKf() {
      // / 
      if (
        this.waitInfo.state == WAIT_STATUS_ENUM.inqueue ||
        this.waitInfo.state == WAIT_STATUS_ENUM.success
      ) {
        return;
      }
      this.chatList.push({
        role: CHAT_ROLE_TYPE_ENUM.roundTip,
        customId: getUId(),
        content: "",
      });
      this.goEnd(null, 100);

      let switchSuss = false;
      // 
      try {
        // 
        const resp = await this.msqueueJoin();
        console.log("==========getQueueStatus", resp);

        if (
          resp.state == WAIT_STATUS_ENUM.success ||
          resp.in_queue_count == 0
        ) {
          // 
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.hangUpCard,
            content: "",
            customId: getUId(),
            kfId: resp.staff_info.user_id,
            kfname: resp.staff_info.nickname,
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.staff,
            contentType: CHAT_CONTENT_TYPE_ENUM.text,
            content: {
              text: `${resp.staff_info.nickname}`,
            },
            customId: getUId(),
            sendSuss: true,
            sendLoading: false,
            showMoreBtn: false,
            moreId: getUId(),
            kfId: resp.staff_info.user_id,
            kfname: resp.staff_info.nickname,
            kfAvatar: resp.staff_info.avatar_url,
          });
          switchSuss = true;
          this.chatInfo.session_id = resp.session_id;
          this.watchChatActive();
        } else if (resp.state == WAIT_STATUS_ENUM.inqueue) {
          // 
          if (this.waitInfo.in_queue_count === resp.in_queue_count) {
            //
            return;
          }
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.roundTip,
            customId: getUId(),
            content: `${resp.in_queue_count}`,
          });
        } else if (resp.state == WAIT_STATUS_ENUM.fail) {
          // 
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.leaveMsgBtn,
            customId: getUId(),
            content:
              "",
          });
          switchSuss = true;
        }
        this.goEnd(null, 100);
        this.waitInfo = resp;
      } catch (error) {
        if (error.message == NETWORK_ERROR_STATUS) {
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "-   -",
          });
          this.goEnd(null, 100);
        } else if (error.message != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.message,
          });
        }
        return;
      }

      // /
      if (switchSuss) {
        return;
      }
      // 
      this.waitSwtichStaff();
    },
    // 
    async handleSendText(_e, issues) {
      let content;
      if (!issues) {
        content = document.querySelector(".chat-footer-input").innerHTML;
        if (!content) {
          // document.querySelector(".chat-footer-input").focus();
          return;
        }
      } else {
        content = issues;
      }
      console.log("content=>", content);

      const meassageItem = {
        role: CHAT_ROLE_TYPE_ENUM.user,
        contentType: CHAT_CONTENT_TYPE_ENUM.text,
        content: {
          text: content,
        },
        sendLoading: true,
        sendSuss: true,
        customId: getUId(),
      };
      this.chatList.push(meassageItem);

      if (!issues) {
        // document.querySelector(".chat-footer-input").focus();
        document.querySelector(".chat-footer-input").innerHTML = "";
        this.chatInputContent = "";
      }
      this.goEnd(null, 100);

      const req = {
        message: content,
        type: CHAT_CONTENT_TYPE_ENUM.text,
      };
      let resp;

      try {
        resp = await this.createMessage(req);
      } catch (error) {
        meassageItem.sendSuss = false;
        return;
      } finally {
        meassageItem.sendLoading = false;
      }
      meassageItem.sendSuss = true;

      if (resp.is_hit_msk) {
        this.chatList.push({
          role: CHAT_ROLE_TYPE_ENUM.switchKfBtn,
          customId: getUId(),
          content: "",
        });
        this.goEnd(null, 100);
      }

      // const deleteIndex = this.chatList.findIndex(
      //   (i) => i.customId == meassageItem.customId
      // );
      // if (deleteIndex > -1) {
      //   this.chatList.splice(deleteIndex, 1);
      // }
      // console.log("=>", deleteIndex);

      // 
      if (this.relateIssueLoading) {
        return;
      }
      this.relateIssueLoading = true;

      await timeout(1000);
      this.getRelateIssueList(content);
    },
    //------------------------------------------------------------------
    // 
    async msqueueJoin() {
      console.log("msqueueJoin invoke");

      const req = {
        session_id: this.chatInfo.session_id,
      };
      let resp;
      try {
        resp = await msqueueJoin(req);
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }
      this.chatInfo.session_id = resp.session_id;
      return resp;
    },
    // 
    async messageQuit() {
      console.log("messageQuit invoke");

      const req = {
        session_id: this.chatInfo.session_id,
      };
      try {
        await messageQuit(req);
      } catch (error) {
        return;
      }
    },
    // 
    async sessionHeartbeat() {
      const req = {
        session_id: this.chatInfo.session_id,
      };

      try {
        await sessionHeartbeat(req);
      } catch (error) {
        return;
      }
    },
    // 
    async createMessage(params) {
      const req = {
        session_id: this.chatInfo.session_id,
        message: params.message,
        type: params.type,
      };

      let resp;
      try {
        resp = await messageSend(req);
      } catch (error) {
        throw new Error(error.msg);
      }
      // 
      this.watchChatActive();
      return resp;
    },
    // 
    async getMessage(params) {
      const req = {
        session_id: this.chatInfo.session_id,
        mode: params.mode,
        start_message_id: params.start_message_id,
        size: PAGE_SIZE_DEFAULT,
      };
      let resp;
      try {
        resp = await messagePull(req);
      } catch (error) {
        return [];
      }
      console.log("======getMessage", resp);

      resp.message_list = resp.message_list.map((i) => {
        return {
          ...i,
          selfId: i.message_id,
        };
      });
      return resp.message_list;
    },
    //------------------------------------------------------------------
    // 
    handleIssuesMoreShow(item) {
      item.showMoreBtn = false;
      const moreDom = document.getElementById(item.moreId);
      moreDom.classList.add("act");
    },
    // 
    handlePicShow(src) {
      this.picSrc = src;
      this.picVisible = true;
    },
    // 
    handlePicHide() {
      this.picVisible = false;
      this.picSrc = "";
    },
    // 
    handleLeaveMessageSuccShow() {
      this.leaveMessageSuccVisible = true;
    },
    // 
    handleLeaveMessageSuccHide() {
      this.leaveMessageSuccVisible = false;
    },
    // 
    async handleRideRecordShow() {
      this.rideRecordVisible = true;
    },
    // 
    handleRideRecordHide() {
      this.rideRecordVisible = false;
    },
    // id
    setFirstMsgId() {
      this.chatList = this.chatList.map((i) => {
        i.id = getUId();
        return i;
      });
      this.firstMsgId = this.chatList[0].id;
    },
    // 
    goEnd(cb, time = 200) {
      this.$nextTick(() => {
        setTimeout(() => {
          if (this.$refs.common_chat_main) {
            // this.$refs.common_chat_main.scrollTop =
            //   this.$refs.common_chat_main.scrollHeight;
            document
              .getElementsByClassName("chat-messages-item")
              [
                document.getElementsByClassName("chat-messages-item").length - 1
              ].scrollIntoView({
                behavior: "auto",
                block: "end",
                inline: "nearest",
              });

            cb && cb();
          }
        }, 0);
      });
    },
    handleTouchPage() {
      // document.querySelector(".chat-footer-input").blur();
    },
    // 
    handleInput(e) {
      this.chatInputContent = e.target.innerHTML;
    },
    // 
    handleInputBlur() {
      let u = navigator.userAgent;
      if (u.indexOf("iPhone") > -1) {
        window.scrollTo(0, 0);
      }
      this.chatInputFocus = false;
    },
    // 
    handleInputFocus() {
      this.$nextTick(() => {
        // this.goEnd(); 
        this.chatInputFocus = true;
      });
    },
    // 
    handleLeaveMessageShow() {
      this.leaveMessageVisible = true;
    },
    // 
    async handleLeaveMessageHide() {
      this.leaveMessageVisible = false;
      await timeout(300);
      this.leaveInfo.contact_info = "";
      this.leaveInfo.issue_desc = "";
    },
  },
};
</script>

